define("appkit/app",
  ["resolver","appkit/router"],
  function(Resolver, router) {
    "use strict";

    var App = Ember.Application.create({
      modulePrefix: 'appkit', // TODO: loaded via config
      Resolver: Resolver,
      Router: Ember.Router.extend({
        router: router,
        location: 'none'
      })
    });


    return App;
  });